<script>

export default {
  name: 'App',
  components: {
  },
  data () {
    return {
      snack: false,
      snackColor: '',
      snackText: '',
      max25chars: v => v.length <= 25 || 'Input too long!',
      pagination: {},
      newRowVisible: false,
      newDebit: "01",
      newCredit: "01",
      newSumm: 0,
      newInfo: "",
      lstDC: [],
      objNums: {},
      headers: [
        {
          text: '№',
          align: 'start',
          // value: 'name',
          value: 'number',
        },
        { text: 'Дебет', value: 'debit'
          // value: 'calories'
        },
        { text: 'Кредит', value: 'credit',
          // value: 'fat'
        },
        { text: 'Сумма',
          value: 'summ'
          // value: 'carbs'
        },
        { text: 'Содержание хозяйственной операции',
          value: 'info',
          // value: 'protein',
          sortable: false }
      ],
      rows: [
        {
          number: 1,
          debit: 51,
          credit: 62,
          summ: 50000,
          info: "Получены деньги за предоставленные услуги",
        },
        {
          number: 2,
          debit: 10,
          credit: 60,
          summ: 5000,
          info: "Оприходован материал на склад",
        },
        {
          number: 3,
          debit: 70,
          credit: 50,
          summ: 45000,
          info: "Выплачена зп работникам",
        }
      ],
    }
  },
  methods: {
    save () {
      this.snack = true
      this.snackColor = 'success'
      this.snackText = 'Data saved'
    },
    cancel () {
      this.snack = true
      this.snackColor = 'error'
      this.snackText = 'Canceled'
    },
    open () {
      this.snack = true
      this.snackColor = 'info'
      this.snackText = 'Dialog opened'
    },
    close () {
      // console.log('Dialog closed')
    },
    addNewRow(e) {
      e.preventDefault();
      this.rows.push({
        number: this.rows.length+1,
        debit: this.newDebit,
        credit: this.newCredit,
        summ: this.newSumm,
        info: this.newInfo
      })
      this.newDebit="01";
      this.newCredit="01";
      this.newSumm=0;
      this.newInfo="";
      this.newRowVisible = false;
    },
    getDebits(num) {
      // console.log(this.rows);
      // console.log("getDebits");
      let res = [];
      let res_sum = 0;
      for (let el of this.rows) {
        if (el.debit == num) {
          res.push(el.summ);
          res_sum += parseInt(el.summ);
        }
      }
      if (!(num in this.objNums)) {
        this.objNums[num] = {debit: 0, credit: 0};
        this.objNums[num].debit = res_sum;
      }
      else {
        this.objNums[num].debit = res_sum;
      }
      // this.objNums[num]=300;
      return res
    },
    getCredits(num) {
      // console.log("getCredits");
      let res = [];
      let res_sum = 0;
      for (let el of this.rows) {
        if (el.credit == num) {
          res.push(el.summ);
          res_sum += parseInt(el.summ);
        }
      }
      if (!(num in this.objNums)) {
        this.objNums[num] = {debit: 0, credit: 0};
        this.objNums[num].credit = res_sum;
      }
      else {
        this.objNums[num].credit = res_sum;
      }
      // this.objNums[num]=300;
      return res
    }
  },
  watch: {
    rows:  {
      handler: function(after, before) {
        let new_arr = [];
        for (let i of after) {
          if (new_arr.indexOf(i.debit.toString())===-1) {
            new_arr.push(i.debit.toString());
          }
          // if (this.lstDC.indexOf(i.debit)==-1) {
          //   this.lstDC.push(i.debit)
          // }
          if (new_arr.indexOf(i.credit.toString())===-1) {
            new_arr.push(i.credit.toString());
          }
          // if (this.lstDC.indexOf(i.credit)==-1) {
          //   this.lstDC.push(i.credit)
          // }
        }
        for (let el of new_arr) {
          this.objNums[el] = {debit: 0, credit: 0};
        }
        this.lstDC = new_arr;
        // for (let i of this.lstDC) {
        //   console.log(i);
        //   if ()
        // }
      },
      deep: true
    }
  },
  mounted() {
    let new_arr = [];
    for (let i of this.rows) {
      // if (this.lstDC.indexOf(i.debit)==-1) {
      //   this.lstDC.push(i.debit)
      // }
      if (new_arr.indexOf(i.debit.toString())===-1) {
        new_arr.push(i.debit.toString());
      }
      if (new_arr.indexOf(i.credit.toString())===-1) {
        new_arr.push(i.credit.toString());
      }
      // if (this.lstDC.indexOf(i.credit)==-1) {
      //   this.lstDC.push(i.credit)
      // }
    }
    this.lstDC = new_arr;
  }
}
</script>

